body{font-family:'Lato', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:calc(10px + 2vmin)}.text-bold{font-weight:bold}

.layout{background-color:#282c34;display:flex;flex-direction:column;min-height:100vh;max-width:100vw}.layout a:-webkit-any-link{text-decoration:none}.layout a:any-link{text-decoration:none}.layout h2{text-align:center;padding:.3em;font-weight:bold}.layout-socialnav{background-color:white}.layout-container{flex:1 1}.layout-content{color:white;padding:.5em}@media screen and (min-width: 600px){.layout-content{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.layout-socialnav{display:none}.layout .menu{display:block;position:static;position:initial}.layout .menu ul{display:flex;justify-content:center;font-size:.8em}}@media screen and (min-width: 1025px){.layout-content{max-width:60vw}}

.menu{background-color:white;position:absolute;top:4.7em;width:100%;display:none;z-index:1}.menu-toggle{display:block}.menu ul{list-style:none}.menu li{padding:.8em;text-align:center}.menu a{color:black}

.socialnav{display:flex;justify-content:center;font-size:1.2em}.socialnav a{color:#20232a;padding:.3em}


.header{background-color:#20232a}.header-content{display:flex;align-items:center;color:white}.header-imgCover{margin-left:.5em;padding:.5em}.header-img{background-color:white;border-radius:50%;width:4em}.header-title{flex:1 1;margin-left:.5em}.header-title small{font-size:.6em;font-style:italic}.header-iconMenu i{font-size:1.5em;padding:.8em}.header-socialnav{display:none}@media screen and (min-width: 600px){.header-container{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.header-socialnav{display:block}.header-socialnav a{color:white}.header-iconMenu{display:none}}@media screen and (min-width: 1025px){.header-container{max-width:50vw}}

.footer{background-color:white;padding:.2em}.footer-content{display:flex;align-items:center;justify-content:space-between}.footer-info{font-size:.8em;font-style:italic}@media screen and (min-width: 600px){.footer-container{max-width:75vw;display:block;margin:0 auto}}@media screen and (min-width: 769px){.footer-container{max-width:60vw}}@media screen and (min-width: 1025px){.footer-container{max-width:50vw}}

